import React from 'react';
import Square from './Square';
import { MIN_DISTANCE_PC, MIN_DISTANCE_M, RIGHT, LEFT, DOWN, UP } from '../utils/constants';

const Board = props => {
  const {
    squares,
    onMove
  } = props;
  let coordinate = {
    X: 0,
    Y: 0
  };
  const SquaresUI = squares.map((n, idx) => {
    return React.createElement(Square, {
      num: n,
      key: idx
    });
  }); // 

  function getDirection(curX, curY, minDistance) {
    // 
    const deltaX = curX - coordinate.X;
    const deltaY = curY - coordinate.Y;
    const absDeltaX = Math.abs(deltaX);
    const absDeltaY = Math.abs(deltaY); // 100

    if (absDeltaX < minDistance && absDeltaY < minDistance) return null; // delta    

    const dirX = deltaX > 0 ? RIGHT : LEFT;
    const dirY = deltaY > 0 ? DOWN : UP;
    const deltaDir = absDeltaX - absDeltaY;
    let direction;

    if (deltaDir > 0) {
      // deltaX - deltaY > 0 ,   
      direction = dirX;
    } else if (deltaDir < 0) {
      // deltaX - deltaY < 0 ,   
      direction = dirY;
    } else {
      // deltaX - deltaY = 0,   
      direction = Math.round(Math.random()) ? dirX : dirY;
    } // reset


    coordinate.X = 0;
    coordinate.Y = 0;
    return direction;
  } // 


  function handleTouchStart(e) {
    e.preventDefault();
    e.stopPropagation(); // console.log('handleTouchStart', e)
    // // TODO: 
    // if (e.touches.length > 1) return

    coordinate.X = e.touches[0].clientX;
    coordinate.Y = e.touches[0].clientY;
  }

  function handleTouchEnd(e) {
    e.preventDefault();
    e.stopPropagation(); // console.log('handleTouchEnd', e)
    // TODO: 
    // if (e.changedTouches.length > 1) return
    // 

    if (!coordinate.X && !coordinate.Y) {
      // reset
      coordinate.X = 0;
      coordinate.Y = 0;
      return;
    }

    const direction = getDirection(e.changedTouches[0].clientX, e.changedTouches[0].clientY, MIN_DISTANCE_M);

    if (direction) {
      onMove(direction);
    }
  } // 


  function handleMouseDown(e) {
    e.preventDefault();
    e.stopPropagation(); // console.log('handleMouseDown', e, e.clientX, e.clientY)

    coordinate.X = e.clientX;
    coordinate.Y = e.clientY;
  }

  function handleMouseUp(e) {
    e.preventDefault();
    e.stopPropagation(); // 

    if (!coordinate.X && !coordinate.Y) {
      // reset
      coordinate.X = 0;
      coordinate.Y = 0;
      return;
    }

    const direction = getDirection(e.clientX, e.clientY, MIN_DISTANCE_PC); // console.log('handleMouseUp', e, e.clientX, e.clientY, direction)

    if (direction) {
      onMove(direction);
    }
  } // 


  function handleKeyUp(e) {
    e.stopPropagation(); // console.log('handleKeyUp', e)

    switch (e.code) {
      case UP:
      case RIGHT:
      case DOWN:
      case LEFT:
        {
          const direction = e.code;

          if (direction) {
            onMove(direction);
          }

          break;
        }
    }
  }

  document.onkeyup = handleKeyUp;
  return React.createElement("div", {
    className: "board",
    onMouseDown: handleMouseDown,
    onMouseUp: handleMouseUp,
    onTouchStart: handleTouchStart,
    onTouchEnd: handleTouchEnd
  }, SquaresUI);
};

export default Board;