import React, { useEffect, useState } from 'react';
import axios, { formatFormUrlencoded } from '../utils/axios';
import RankList from '../components/RankList';
import Modal from '../components/Modal';
import Loading from '../components/Loading';
import '../scss/ranking.scss';
/* *
 * 
 * */

const Ranking = props => {
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [list, setList] = useState([]);
  const controller = new AbortController();
  useEffect(() => {
    axios.post('', formatFormUrlencoded({
      action: 'get'
    }), {
      signal: controller.signal
    }).then(e => {
      if (e.data && Array.isArray(e.data.rank_data)) {
        setList(e.data.rank_data);
        setIsLoading(false);
      } else {
        throw new Error('response is error');
      }
    }).catch(err => {
      console.error('err=', err);
      setIsLoading(false);
      setError(err);
    });
    return () => controller.abort();
  }, []);
  const ResUI = error ? React.createElement("p", {
    className: "error"
  }, "Opps! Something wrong. Please try again later.") : React.createElement(RankList, {
    list: list
  });
  return React.createElement("div", {
    className: "ranking"
  }, React.createElement("h1", {
    className: "title"
  }, "RANKING"), isLoading ? React.createElement(Modal, null, React.createElement(Loading, null)) : ResUI);
};

export default Ranking;