import { useEffect } from 'react';
import ReactDOM from 'react-dom';
/**
 * modal portal
 **/

const Modal = props => {
  const modalRoot = document.getElementById('modal'); // const [wrapperEle] = useState<HTMLElement>(document.createElement('div'))

  const wrapperEle = document.createElement('div'); // #modaltoastmodalhookmodal

  useEffect(() => {
    //  Modal 
    //  portal  DOM 
    //  DOM 
    //  DOM 
    //  DOM 
    //  autoFocus
    //  state  Modal 
    //  Modal  DOM 
    modalRoot.appendChild(wrapperEle);
    return () => {
      modalRoot.removeChild(wrapperEle);
    };
  }, []);
  return ReactDOM.createPortal(props.children, wrapperEle);
};

export default Modal;