import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios, { formatFormUrlencoded } from '../utils/axios';
import Modal from '../components/Modal';
import Loading from '../components/Loading';
import ResultModalFirstPage from './ResultModalFirstPage';
import ResultModalSecondPage from './ResultModalSecondPage';
/**
 * modal
 **/

const ResultModal = props => {
  let navigate = useNavigate();
  const [modalClass, setModalClass] = useState('modal');
  const [scorer, setScorer] = useState('');
  const [pageNum, setPageNum] = useState(1); // 

  const [isSubmit, setIsSubmit] = useState(false); // 

  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [list, setList] = useState([]);
  const [myInfo, setMyInfo] = useState({
    id: -1,
    rank_num: 0,
    user_name: scorer,
    user_score: props.score // 

  });
  useEffect(() => {
    setModalClass(props.isShow ? 'modal show' : 'modal');
  }, [props.isShow]); // 

  const closeModal = typeName => {
    switch (typeName) {
      case 'restart':
        {
          props.onRestart();
          setModalClass('modal'); // 0.5s0.2sDom

          setTimeout(() => {
            props.onClose();
          }, 200);
          break;
        }

      case 'home':
        {
          navigate(-1);
          break;
        }
    }
  };

  const onChange = e => setScorer(e.target.value);

  const onSubmit = () => {
    if (isLoading) return;

    if (!scorer) {
      setError('Please enter your name');
      return;
    }

    if (error) {
      setError('');
    }

    setIsLoading(true);
    axios.post('', formatFormUrlencoded({
      action: 'add',
      user_name: scorer,
      user_score: props.score
    })).then(e => {
      if (e.data && e.data.current_data) {
        setMyInfo(e.data.current_data);
        setList(e.data.rank_data);
        setIsLoading(false);
        setIsSubmit(true);
        setPageNum(2);
      } else {
        throw new Error('response is error');
      }
    }).catch(err => {
      console.error('err=', err);
      setIsLoading(false);
      setError(err);
    });
  };

  return React.createElement("div", {
    className: modalClass
  }, React.createElement("div", {
    className: "modal-main"
  }, isLoading ? React.createElement(Modal, null, React.createElement(Loading, null)) : null, pageNum === 1 ? React.createElement(ResultModalFirstPage, {
    score: props.score,
    bestScore: props.bestScore,
    isError: Boolean(error),
    onChange: onChange,
    onSubmit: onSubmit,
    onPageChange: e => setPageNum(e)
  }) : React.createElement(ResultModalSecondPage, {
    isSubmit: isSubmit,
    list: list,
    myInfo: myInfo,
    score: props.score,
    bestScore: props.bestScore,
    onClose: e => closeModal(e)
  })));
};

export default ResultModal;